.spinnerContainer{
    width: 100%;
    background-color: rgba(255, 255, 255, 0);
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.spinner{
    background-color: rgb(56, 56, 56);
    width: 70px;
    height: 70px;
    display: flex;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    animation: spinner 1.5s linear 0s infinite forwards;

}

.innerSpinner{
    background-color: #f1f1f1;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: visible;
    animation: innerSpinner 1s ease-in-out 0s infinite forwards alternate-reverse;

}

.spinnerBar{
    background-color: #f1f1f1;
    min-width: 120px;
    height: 20px;
    animation: spinnerBar 1s ease-in-out 0s infinite forwards alternate-reverse;
}

@keyframes spinnerBar {
    from{
        height: 20px
    }
    to{
        height:40px
    }
}

@keyframes innerSpinner {
    from{
        height: 40px;
        width: 40px;
    }
    to{
        height:60px;
        width: 60px;
    }
}

@keyframes spinner {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(359deg);
    }
}
